export default {
  data: function data() {
    return {
      animationList: [],
      codeValue: ''
    };
  },
  created: function created() {
    this.getAnimation();
  },
  methods: {
    displayAnimation: function displayAnimation(animation) {
      this.$router.push({
        path: 'animation/' + animation.name
      });
    },
    getAnimation: function getAnimation() {
      var _this = this;

      this.$http.get(this.INTERFACE.getAnimations).then(function (res) {
